import React, { createContext, useContext } from "react"

const categorys = [
  {
    id : 1,
    category : '',
    selected : true
  },
  {
    id : 2,
    category : '',
    selected : false
  },
  {
    id : 3,
    category : '',
    selected : false
  },
  {
    id : 4,
    category : 'e-book',
    selected : false
  },
  {
    id : 5,
    category : '',
    selected : false
  },
  {
    id : 6,
    category : '',
    selected : false
  },
  {
    id : 7,
    category : '/Gift',
    selected : false
  },
  {
    id : 8,
    category : '/',
    selected : false
  },
  {
    id : 9,
    category : '',
    selected : false
  }
]

export const SearchCategoryContext = createContext()

export function SearchCategoryProvider({children}){
  return(
    <SearchCategoryContext.Provider value={categorys}>
      {children}
    </SearchCategoryContext.Provider>
  )
}

export function useSearchCategory(){
  const context = useContext(SearchCategoryContext)
  if(!context){
    throw new Error('SearchCategoryContext Error')
  }
  return context
}